import { hydrateRoot } from 'react-dom/client';
import './styles/style.css';
import {
  HobuyWidget,
  type HobuyWidgetProps,
} from './components/widget-container';

function initializeWidget() {
  console.log('[HobuyWidget] Initializing...');

  if (document.readyState !== 'loading') {
    onReady();
  } else {
    document.addEventListener('DOMContentLoaded', onReady);
  }
}

function onReady() {
  window.HobuyWidget = {
    init: (options: HobuyWidgetProps) => {
      try {
        const element = document.createElement('div');
        const shadow = element.attachShadow({ mode: 'open' });
        const shadowRoot = document.createElement('div');

        shadowRoot.id = 'widget-root';

        const component = <HobuyWidget injectToBody={false} {...options} />;

        shadow.appendChild(shadowRoot);
        injectStyle(shadowRoot);
        hydrateRoot(shadowRoot, component);

        document.body.appendChild(element);
      } catch (error) {
        console.warn('[HobuyWidget] Widget initialization failed:', error);
      }
    },
  };
}

function injectStyle(shadowRoot: HTMLElement) {
  const link = document.createElement('link');
  link.rel = 'stylesheet';
  const fileName = process.env.WIDGET_NAME || 'widget';
  link.href = process.env.WIDGET_CSS_URL || `/${fileName}.css`;
  shadowRoot.appendChild(link);
}

initializeWidget();
